.exercises {
  max-width: 60rem;
  width: 90%;
  margin: 2rem auto;
  animation: all-exercises-appear 1s ease-out forwards;
  text-align: center;
}

.exercises ul {
  color: white;
  list-style: none;
  margin: 0;
  padding: 0;
  animation: exercises-appear 1s ease-out forwards;
}

.text {
  text-align: left;
}

.ExercisesLoading {
  text-align: center;
  color: white;
}

.ExercisesError {
  text-align: center;
  color: white;
}

.exercises button {
  font-size: xx-large;
  cursor: pointer;
  background-color: black;
  border: 1px solid transparent;
  color: white;
  padding: 0.25rem 2rem;
  width: 265px;
  border-radius: 20px;
  font-weight: bold;
  margin-top: 1rem;
}

.exercises button:hover,
.exercises button:active {
  background-color: #3a3a3a;
}

.exercises .button--alt {
  font-size: large;
  border-radius: 10px;
  float: right;
  animation: exercises-appear 1s ease-out forwards;
  width: auto;
}

/* .exercises .button--main {
  font-size: large;
  border-radius: 10px;
  float: right;
  animation: exercises-disappear 1s ease-out forwards;
} */

.clearfix {
  clear: both;
}

hr {
  border: none;
  background-color: black;
  height: 3px;
}

@keyframes all-exercises-appear {
  from {
    opacity: 0;
    transform: translateY(3rem);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes exercises-appear {
  from {
    opacity: 0;
    transform: translateY(-3rem);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media only screen and (max-width: 500px) {
  .exercises button {
    font-size: large;
    width: 150px;
  }
}
