.form {
  text-align: right;
}

.form button {
  cursor: pointer;
  background-color: black;
  border: 1px solid black;
  color: white;
  padding: 0.25rem 2rem;
  border-radius: 20px;
  font-weight: bold;
}

.form button:hover,
.form button:active {
  background-color: #3a3a3a;
  border-color: #3a3a3a;
}
