.exercise {
  display: flex;
  justify-content: space-between;
  margin: 1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #ccc;
  align-items: center;
}

.exercise p {
  margin: 0 0 0.25rem 0;
  font-size: 30px;
  font-weight: bold;
  padding-bottom: 10px;
}

.description {
  font-style: italic;
  font-size: 20px;
  font-weight: 300;
}

