.summary {
  text-align: center;
  max-width: 45rem;
  width: 90%;
  margin: auto;
  margin-top: -10rem;
  position: relative;
  background-color: #807e7d;
  color: white;
  border-radius: 14px;
  padding: 1rem;
  box-shadow: 0 1px 18px 10px rgba(0, 0, 0, 0.25);
  font-size: 22px;
  font-weight: 500;
}

.summary h2 {
  font-size: 40px;
  margin-top: 0;
  font-style: italic;
  letter-spacing: 15px;
  font-weight: 800;
}
