.plan-items {
  list-style: none;
  margin: 0;
  padding: 0;
  height: auto;
  max-height: 500px;
  overflow-y: scroll;
}

.total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 1.5rem;
  margin: 1rem 0;
}

.actions {
  text-align: right;
}

.actions button {
  font: inherit;
  cursor: pointer;
  background-color: transparent;
  border: 1px solid black;
  padding: 0.5rem 2rem;
  border-radius: 25px;
  margin-left: 1rem;
}

.actions button:hover,
.actions button:active {
  background-color: #3a3a3a;
  border-color: #3a3a3a;
  color: white;
}

.actions .button--alt {
  color: black;
}

.actions .button {
  background-color: black;
  color: white;
}

@media only screen and (max-width: 800px) {
  .plan-items {
    max-height: 300px;
  }
}
