.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background-color: rgb(24, 24, 24);
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* padding-left: 20px; */
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}

.main-image {
  width: 100%;
  height: 25rem;
  z-index: 0;
  overflow: hidden;
}

.main-image img {
  width: 110%;
  height: 100%;
  object-fit: cover;
  transform: rotateZ(-5deg) translateY(-4rem) translateX(-1rem);
}

.button {
  background-color: #807e7d;
  color: white;
  font-size: xx-large;
  font-weight: bold;
  border: none;
  border-radius: 25px;
  margin-left: 50px;
  padding-left: 20px;
  padding-right: 20px;
  cursor: pointer;
}

.button:hover {
  background-color: #3a3a3a;
}

@media only screen and (max-width: 800px) {
  .button {
    font-size: large;
    margin-left: 20px;
    height: 2.5rem;
  }
}
